#keyboard {
  flex-wrap: nowrap;
  display: flex;
  position: relative;
}

.key {
  height: 100px;
  width: 30px;
  z-index: 1;
  border: 1px solid #ccc;
}

.key-white {
  background-color: #fff;
}

.key-black {
  height: 60px;
  width: 20px;
  z-index: 2;
  background-color: #000;
  margin-left: -10px;
  margin-right: -10px;
}

.key.pressed {
  background-color: #6db5ff;
}

.key.missed {
  background-color: #ff6d6d;
}

.key.expected {
  background-color: #6dff6d;
}

#trainingIndicator {
  color: #ccc;
  font-size: 3em;
  font-weight: bold;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

